/* Container principal */
/* Cont√™iner da tabela */
#fornecedores-grid-container {
  overflow-x: auto; /* Adiciona rolagem horizontal se necess√°rio */
}

/* Tabela */
#fornecedores-grid {
  width: 100%;
  border-collapse: collapse;
}

#fornecedores-grid th, #fornecedores-grid td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

#fornecedores-grid th {
  background-color: #f4f4f4;
}

#fornecedores-grid td#button-action {
  text-align: center;
}



#pessoa-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

/* T√≠tulo */
#pessoa-title {
  text-align: center;
  margin-bottom: 20px;
}


/* Campo Nome */
#nome-group {
  display: flex;
  flex-direction: column;
  align-items: right;
  width: 300px;
  margin-left: 5%;
  margin-top: 5%;

}

#nome-group label {
  margin-bottom: 5px;
  font-weight: bold;
}

#nome-group input {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  max-width: 100%;
}

/* Campo CPF */
#cpf-group {
  display: flex;
  flex-direction: column;
  align-items: right;
  width: 200px;
  margin-left: 5%;
  margin-top: 5%;
}

#cpf-group label {
  margin-bottom: 5px;
  font-weight: bold;
}

#cpf-group input {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  max-width: 100%; 
}