/* src/styles/ModalCadastroFornecedor.css */

/* Conte√∫do do Modal */
.modal-content-cad-for {
  background: #fff;
  padding: 20px;
  border-radius: 8px; /* Aumentado o raio para um efeito mais suave */
  width: 85%;
  max-height: 90vh; /* Limita a altura do modal para 90% da altura da viewport */
  overflow-y: auto; /* Adiciona rolagem vertical se necess√°rio */
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Adicionada sombra para profundidade */
}

#cadastro-fornecedor {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Duas colunas */
  gap: 20px; /* Espa√ßamento entre os elementos */
  width: 100%;
  position: relative;

}

#cadastro-fornecedor div {
  display: flex;
  flex-direction: column;
}

#cadastro-fornecedor input {
  width: 100%; /* Ou o tamanho desejado */
  box-sizing: border-box; /* Garante que o padding seja inclu√≠do no tamanho total */
}

#cadastro-fornecedor button {
  width: 25%; /* Ou o tamanho desejado */
  box-sizing: border-box; /* Garante que o padding seja inclu√≠do no tamanho total */
}
